<template>
    <div class="container">
        <div class="login__form">
            <div class="title">Вход</div>
            <div class="input-flex input-wrap mb20">
                <label class="calc-block__w1">E-mail</label>
                <input v-model="email" type="text">
            </div>
            <div class="input-flex input-wrap mb20">
                <label class="calc-block__w1">Пароль</label>
                <input v-model="password" type="password">
            </div>
            <div class="support__form__button flex-start">
                <label class="calc-block__w1"></label>
                <button @click="submit" class="button-gradient">Войти</button>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    export default {
        name: "Login",
        data: () => ({
            email: null,
            password: null
        }),

        methods: {
            ...mapActions(['login']),
            submit() {
                this.login({email: this.email, password: this.password}).then(() => {
                    this.$router.push('/')
                })

            }
        },
    }
</script>

<style scoped>
    .login__form {
        background-color: #ffffff;
        padding: 50px 40px;
        width: 500px;
        margin: 50px auto;
    }

    .login__form .title {
        font-weight: bold;
        font-size: 36px;
        margin-bottom: 20px;
    }

    .calc-block__w1 {
        width: 70px;
    }
</style>